.App-logo {
  position: fixed;
  top: 2%;
  left: 2%;
  height: 7vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: brown;
}

#drum-machine {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 60vw;
  height: 45vw;
  background-color: rgba(10, 21, 25, 0.58);
  box-shadow: black 5px 5px 15px;
  border-radius: 4%;
}

.pads {
  width: 65%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 2.5%;
  background-color: rgba(14, 47, 60, 0.34);
}

.drum-pad {
  display: flex;
  flex-basis: 29%;
  margin: 2%;
  justify-content: end;
  align-items: end;
  padding: 1% 2%;
  user-select: none;
  background-color: rgb(38, 107, 107);
  color: rgb(68, 32, 32);
  height: 30%;
  aspect-ratio: 1 / 1;
  border-radius: 7%;
  box-shadow: black 3px 3px 5px;
}

.drum-pad:hover {
  cursor: pointer;
}

.volume {
  position: absolute;
  width: calc(100vw * 0.6 * 0.05);
  height: calc(100vw * 0.4 * 0.4);
  left: 5%;
}

