.display {
  width: 50%;
  height: 10%;
  background-color: rgb(7, 22, 28);
  font-family: 'Digital 7 mono';
  font-size: 2.5rem;
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

@media (max-width: 66.5em) {
  body {
    font-size: 1.2rem;
  }

  #drum-machine {
    transform: scale(1.3);
  }

  .display {
    font-size: 2.2rem;
  }
}

@media (max-width: 40.75em) {
  body {
    font-size: 1rem;
  }
  
  .display {
    width: 65%;
    font-size: 1.8rem;
  }

  #drum-machine {
    transform: scale(1.5);
  }
}

@media (max-width: 26.25em) {
  body {
    font-size: 1.2rem;
  }

  #drum-machine {
    height: 60vh;
    width: 70vw;
    transform: scale(1.3);
  }

  .pads {
    width: 65vw;
    height: unset;
    aspect-ratio: 1 / 1;
  }

  .drum-pad {
    width: 25vw;
    height: unset;
  }
  .display {
    width: 65vw;
    height: 5vh;
    margin-top: 1rem;
  }

  .power {
    position: relative;
    align-self: flex-end;
    width: 13vw;
    height: 5%;
  }

  .volume {
    position: relative;
    align-self: flex-start;
    width: unset;
    height: unset;
    left: 2.5vw;
    margin: 0.2rem 0;
  }
}
